*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }
  @media only screen and (max-width: 1800px) {
    html {
      font-size: 75%; } }
  @media only screen and (max-width: 1200px) {
    html {
      font-size: 56.25%; } }
  @media only screen and (max-width: 900px) {
    html {
      font-size: 50%; } }

body {
  font-family: "Montserrat", sans-serif;
  color: #f9f8f6;
  font-weight: 300;
  line-height: 1.6; }

#root {
  display: grid;
  grid-template-rows: 1fr;
  background-color: #f9f8f6; }
  @media only screen and (max-width: 430px) {
    #root {
      grid-template-columns: initial; } }

.App {
  grid-column: 1/3; }

.header {
  grid-column: 2; }
  @media only screen and (max-width: 500px) {
    .header {
      padding: 0rem 0rem; } }

a {
  text-decoration: none;
  color: black; }

.header {
  display: grid;
  grid-template-columns: min-content 1fr 0.5fr;
  align-items: center;
  color: white;
  font-size: 2rem;
  background-color: #d22030;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 999; }
  @media only screen and (max-width: 700px) {
    .header {
      grid-template-columns: 1fr;
      justify-items: center;
      padding: 2rem 1rem; } }
  .header__left--list, .header__right--list {
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    font-size: 1.5rem;
    grid-template-columns: min-content auto;
    align-items: center; }
  .header__left--item, .header__right--item {
    margin-right: 1rem;
    font-weight: 500; }
    .header__left--item:hover, .header__right--item:hover {
      opacity: 0.5;
      cursor: pointer; }
  .header__logo {
    margin-left: 2rem; }

.button__signup, .button__login {
  color: white;
  padding: 1rem 2rem 1rem 2rem;
  border-radius: 3rem;
  transition: all 0.2s; }
  .button__signup:hover, .button__login:hover {
    opacity: 0.5;
    cursor: pointer; }

.banner {
  grid-column: full-start / full-end;
  background-image: linear-gradient(to right bottom, rgba(255, 255, 255, 0.35), rgba(255, 255, 255, 0.35)), url("https://schwans-prod.imgix.net/images/campaigns/photos/000/041/287/width_800/1536238305DSC02778.JPG?ch=Width%2CDPR%2CSave-Data&auto=format%2Ccompress&dpr=2&w=650");
  background-size: cover;
  background-repeat: no-repeat;
  display: grid;
  grid-template-rows: repeat(3, minmax(min-content, 7.5rem));
  grid-template-columns: 1fr;
  padding-bottom: 20rem;
  height: 100vh;
  justify-content: center; }
